<template>
    <div>
        <div>{{count[0]+count[1]}}</div>
        <el-form :inline=true :model="form">
            <el-form-item label="记事项"> 
            <el-input v-model="form.conten"></el-input>
            </el-form-item>
            <el-form-item label="时间">
            <el-date-picker v-model="form.time" type="date"  value-format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button @click="save">保存</el-button>
            </el-form-item>
        </el-form>
        <list-item  v-for="(li,ind) in list" :key="ind"  :data="li" :index="ind"  @to-unfinish="li.state=false"    @to-finish="li.state=true"  @to-del="del" ></list-item>
        <p> 已完成:{{count[0]}} </p>
        <p> 未完成:{{count[1]}} </p>
<el-button type="danger" @click="clear">清空</el-button>
    </div>
</template>
<script>
import ListItem from './components/ListItem'
export default {
    name:"TodoList",
    components:{ListItem},
    data(){
        return {
            form:{
                conten:"",
                time:"",
            },
            list:[]
        }
    },
    computed:{
        count(){
            let len=0;
            let len2=0;
            this.list.map(li=>{
                if(li.state){
                    len++
                }else{
                    len2++;
                }
            })
            return [len,len2]
        }
    },
    methods:{
        save(){
            this.list.push({
                state:false,
                conten:this.form.conten,
                time:this.form.time
            })
            this.form={
                conten:"",
                time:"",
            }
        },
        del(ind){
            console.log(ind)
          this.list.splice(ind,1) 
        },
        clear(){
            this.list=[];
        }
    }
}
</script>





