<template>
    <div>
        <el-row>
            <el-col :span="12">
                <span v-if="!data.state" >{{data.conten}}---{{data.time}}</span>
                <del v-else>{{data.conten}}---{{data.time}}</del>
            </el-col>
            <el-col :span="12">
                <el-button type="success" v-if="data.state"  @click="$emit('to-unfinish',data)" >已完成</el-button>
                <el-button type="warning" v-else="!data.state" @click="$emit('to-finish',data)" >未完成</el-button>
                <el-button icon="el-icon-delete" circle @click="$emit('to-del',index)"></el-button>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    name:"ListItem",
    props:["data","index"]
    
}
</script>


